* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    background: rgb(16, 16, 48);
    color: white;
    -webkit-font-smoothing: antialiased;
}

input,
button {
    font-size: 1rem;
    font-family: poppins;
    cursor: pointer;
    padding: 4px 10px;
    border-radius: 12px;
    border: 0;
}

#btn {
    margin-left: 12px;
}

#mainbox {
    justify-content: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.searchbox {
    margin: auto;
    margin-bottom: 80px;
    margin-top: 80px;
    display: flex;
    align-items: center;
    width: 100vw;
    justify-content: center;
}

.searchbox input {
    width: 350px;
}

.box {
    float: left;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    background-size: contain;
    margin: 10px;
    width: fit-content;
    flex-shrink: 1;
    height: fit-content;
}

.main {
    border-radius: 12px;
    width: auto;
    flex-shrink: 1;
    flex-grow: 1;
    height: 200px;
    background-size: contain;
}

.txt {
    opacity: 0;
    position: absolute;
    padding: 0px 14px;
    margin-top: 10px;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.txt img {
    height: 45px;
    width: 45px;
    border-radius: 50%;
}

.usertext {
    margin-left: 10px;
    font-size: 0.7rem;
}

.box:hover>.txt {
    opacity: 1;
}